@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
.App {
  background-color: #40407a;
  height: 100vh;
  display: flex;
  align-items: center;
  font-family: "Quicksand", sans-serif;
}

.home_Container {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h3 {
  color: white;
  font-size: 50px;
  font-weight: 500;
  padding: 10px 0;
}

.btn {
  color: aliceblue;
  text-decoration: none;
}

.playBtn {
  color: aliceblue;
  background-color: rgb(24, 192, 24);
  border: none;
  width: 18%;
  padding: 15px 40px;
  border-radius: 30px;
  font-size: 15px;
  cursor: pointer;
}

.quiz_Container {
  background: #f8f8f8;
  width: 80%;
  margin: auto;
  border-left: 5px solid #1da1f2;
  display: flex;
  flex-direction: column;
  padding: 60px 20px;
  box-sizing: border-box;
}

h1 {
  font-size: 50px;
  text-align: center;
  font-weight: 350;
}

.question_container {
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
  font-size: larger;
  color: rgb(96, 97, 97);
}

.option_Container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.options {
  color: white;
  background-color: #1da1f2;
  padding: 20px 10px;
  margin: 20px;
  border-radius: 30px;
  width: 40%;
  text-align: center;
  cursor: pointer;
}

.options:hover {
  background-color: #0c85d0;
}

.button_Container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.button_Container button {
  padding: 5px 25px;
  margin: 0 10px;
  color: #f8f8f8;
  border: none;
  box-shadow: 0px 3px 5px rgba(83, 82, 82, 0.555);
  cursor: pointer;
}

.prev {
  background-color: #0c85d0;
}

.button_Container button:nth-child(2) {
  background-color: #57b846;
}

.quit {
  background-color: #ea4355;
}

.result_Container {
  width: 60%;
  height: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.head {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: rgb(88, 182, 93);
  font-size: 30px;
  margin-top: -10px;
  margin-bottom: 10px;
}

.body {
  background-color: aliceblue;
  padding: 30px 10px;
}

.h1,
.h2 {
  text-align: center;
  font-size: 30px;
  padding: 10px 0;
}

.h2 {
  font-weight: bolder;
  font-size: 40px;
  color: #569ed8;
  margin-bottom: 10px;
}

.body div {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  padding: 5px 20px;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.733);
}
.bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px 0;
}

.bottom button {
  margin: 0 10px;
  padding: 5px 20px;
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
}

.play {
  color: #569ed8;
  border: 3px solid #569ed8;
}

.home {
  color: #98be5e;
  border: 3px solid #98be5e;
}

